import React from 'react';
import classNames from 'classnames';
import {Link} from 'react-router';

import './OrderBill.scss';

const promoCodes = {
    may31x: 2,
    jun30x: 4,
    big10x: 10
}

class OrderBill extends React.Component{
  constructor(props){
    super(props);
    var today = new Date();
    var tomorrow = new Date();
    tomorrow.setDate(today.getDate()+1);
    this.state = {
        startDate: today,
        endDate: tomorrow,
        startDateString: this.getDateString(today),
        endDateString: this.getDateString(tomorrow),
        endDateYearString: this.getYearString(tomorrow),
        days: 2,
        showDatePicker: false,
        promoCode: 'may31x',
        discount: 2,
        errorCode: false
    };
  }

  onInputChange = (value, name) => {
      let state = this.state;
      state[name] = value;
      if (name === 'promoCode') {
          if (promoCodes[value]) {
              state['discount'] = promoCodes[value];
              state['errorCode'] = false;
          } else {
              state['discount'] = 0;
              if (value.length >= 6) {
                  state['errorCode'] = true;
              } else {
                  state['errorCode'] = false;
              }
          }
      }
      this.setState({state});
  };

  goToPaymentDetail = () => {
      this.props.router.push('/payment-detail');
  }
  getDateString = (date, shouldGetWeekDay = false) => {
      var str = date.toDateString();
      if (!shouldGetWeekDay) {
          return `${str.substring(0, 3)}, ${str.substring(8, 10)} ${str.substring(4, 7)}`;
      } else {
          return `${str.substring(8, 10)} ${str.substring(4, 7)}`;
      }
  }

  getYearString = (date) => {
      return `${date.getFullYear()}`;
  }

  render(){
    const {startDate, endDate, days, startDateString,
        endDateString, endDateYearString, showDatePicker, promoCode, discount, errorCode} = this.state;
    return(
      <div>
      <div className="table-section">
          <table className="table order-table">
              <thead>
              <tr>
                  <th>Order</th>
                  <th>Details</th>
                  <th className="third-column"></th>
                  <th className="text-right">Total($)</th>
              </tr>
              </thead>
              <tbody>
              <tr>
                  <td><strong>{days} Day Coverages</strong></td>
                  <td>{this.getDateString(startDate,true)} - {this.getDateString(endDate, true)} {this.getYearString(endDate)}</td>
                  <td></td>
                  <td className="text-right">{days * 4}.00</td>
              </tr>
              <tr>
                  <td></td>
                  <td></td>
                  <td className="text-right">Promo Code Applied({promoCode}):</td>
                  <td className="text-right">
                      {discount? `-${discount}.00`: '0.00'}
                  </td>
              </tr>
              <tr>
                  <td></td>
                  <td></td>
                  <td className="text-right"><strong>Total:</strong></td>
                  <td className="text-right total-number">
                      {days * 4 - discount > 0 ? days * 4 - discount : 0}.00
                  </td>
              </tr>
              </tbody>
          </table>

      </div>

      <div className="promo-code-section">
          <div className="form-group">
              <label htmlFor="code" className="promo-text">Promo Code, if any:</label>
              <input type="input" className="form-control promo-input" id="code"
                  value={promoCode}
                     onChange={(e) => {
                         this.onInputChange(e.target.value, 'promoCode')
                     }}/>
              <span className={classNames("text-danger", {"d-none": !errorCode})}>Promo Code is not correct</span>
          </div>
          <button className="btn btn-green btn-checkout" onClick={this.props.onSubmit}>CHECKOUT NOW </button>
      </div>
      </div>
    );
  }
}

export default OrderBill;



