import axios from 'axios';

export const UPDATE_DATE_RANGE = 'UPDATE_DATE_RANGE';
export const GET_WEATHER_DATA = 'GET_WEATHER_DATA';

const OPEN_API_KEY ='random';


export function updateDateRange(startDate, endDate) {
    return (dispatch) => {
        return dispatch({
            type: UPDATE_DATE_RANGE,
            data: {
                startDate: startDate,
                endDate: endDate
            }
        })
    }
}

export function getWeatherData(startDate, endDate) {
  console.log('process.env: ',process.env);
  const url = `http://api.openweathermap.org/data/2.5/find?q=Singapore&mode=json&units=metric&cnt=9&apikey=${OPEN_API_KEY}`;
  console.log('startDate: ',startDate);
  console.log('endDate: ',endDate);
    return (dispatch) => {
      axios.get(url).then((result) => {
        return dispatch({
            type: GET_WEATHER_DATA,
            data: {
                payload: result.data
            }
        })
      });

    }
}

const actionsMap = {
    [UPDATE_DATE_RANGE]: (state, action) => {
        return {
            ...state,
            dateRange: action.data
        }
    },
    [GET_WEATHER_DATA]: (state, action) => {
        return {
            ...state,
            weather: action.data
        }
    }
};


const initialState = {
    dateRange: {
        startDate: new Date(),
        endDate: new Date()
    }
};

export default function dashboardReducer(state = initialState, action) {
    const handler = actionsMap[action.type];
    return handler ? handler(state, action) : state;
}



